import gql from 'graphql-tag'

import {
  FAddressFragmentDoc,
  FCustomerFragmentDoc,
} from '../../../graph/generated'

export const OrderTickerFragment = gql`
  fragment GOrderListTicker on Order {
    id
    createdAt
    isPaidWithCard
    deliveryType {
      enum
    }
    deliverAt
    deliverAtWorst
    deliveredAt
    delayedDeliveryTime
    companyBranch {
      id
      name
      oldOrderThreshold
    }
    address {
      ...FAddress
    }
    customer {
      ...FCustomer
    }
    totalSum {
      value
      currency {
        code
      }
    }
  }
  ${FAddressFragmentDoc}
  ${FCustomerFragmentDoc}
`
