import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import {
  FEmailFragmentDoc,
  FPhoneFragmentDoc,
  FPriceFragmentDoc,
} from '../../../graph/generated'
import { GOrderDetail } from '../../../graph/types'
import { OrderDriverFragment } from '../molecules/OrderStateDeliver.graphql'

export const QueryOrderDetail = buildQuery<
  GOrderDetail.Query,
  GOrderDetail.Variables
>(gql`
  query GOrderDetail($id: ID!) {
    order(id: $id) {
      id
      createdAt
      deliverAt
      delayedDeliveryTime
      companyBranch {
        id
      }
      customer {
        emails {
          ...FEmail
        }
        phones {
          ...FPhone
        }
      }
      customerOrdersCountCurrent
      customerOrdersCountTotal
      note
      previousNote
      allAppliedRules
      items {
        ...GOrderDetailWare
        sidedishes: sideDishes {
          ...GOrderDetailWare
        }
      }
      totalSum {
        ...FPrice
      }
      transportFee {
        ...FPrice
      }
      coverPrice {
        ...FPrice
      }
      totalDiscount {
        ...FPrice
      }
      orderOrigin {
        code
        enum
      }
      orderTiming: orderStateCategoryRelations {
        createdAt
        orderStateCategory
      }
      ...GOrderDriver
    }
  }

  fragment GOrderDetailWare on OrderRecipe {
    id
    recipe {
      id
      code
      nameLabel
    }
    price {
      ...FPrice
    }
  }
  ${FPriceFragmentDoc}
  ${FEmailFragmentDoc}
  ${FPhoneFragmentDoc}
  ${OrderDriverFragment}
`)
